
var apiList = [
  { chainId: 1, name: "Mainnet", url: "https://mainnet.nebulas.io" },
  { chainId: 1001, name: "Testnet", url: "https://testnet.nebulas.io" },
  { chainId: 100, name: "Local Nodes", url: "http://127.0.0.1:8685"}
];
var nebulas = require("nebulas")
var neb = new nebulas.Neb()
neb.setRequest(new nebulas.HttpRequest(apiList[1].url))
var contractAddress = 'n1jFAbPbyU9ZWuwfmEkvdYT6WoYpt3CBnuP'
var grobal_state = {today: false, pool: new BigNumber(0), activeDays: 0, sort: 0, activePerson: 0}

function commonCall(from, funtion, args, successCB, failedCB) {
  neb.api.call({
    from: from,
    to: contractAddress,
    value: '0',
    nonce: '0',
    gasPrice: '1000000',
    gasLimit: '2000000',
    contract: {"function": funtion,"args": args}
  }).then(successCB).catch(failedCB)
}


const state = {
  _current_lan: 'English',
  _languages: ['English', ''],
  _currAccount: '',
  _state: {today: false, pool: 0, activeDays: 0, sort: 0, activePerson: 0}
}

const getters = {
    getCurLan (state) {
      return state._current_lan
    },
    getSuportLans(state){
      return state._languages
    },
    getCurAccount(state){
      return state._currAccount
    },
    getAccountState(state){
      return state._state
    }
}

const mutations = {
  setCurrentLan:(state, lan)=>{ state._current_lan = lan },
  setCurAccount: (state, account) => { state._currAccount = account },
  setAccountState: (state,accountState) => { state._state = accountState}
}

const actions = {
  changeLan (context, lan) {
    context.commit('setCurrentLan', lan)
  },
   getWallectInfo(context) {
     window.postMessage({
       "target": "contentscript",
       "data": {},
       "method": "getAccount",
     }, "*");
     window.addEventListener('message', function (e) {
       if (e.data && e.data.data) {
         if (e.data.data.account) {//
           context.commit('setCurAccount', JSON.stringify(e.data.data.account))
           context.dispatch('checkMySort', e.data.data.account)
           context.dispatch('checkPool', e.data.data.account)
           context.dispatch('checkSate',e.data.data.account)
           context.dispatch('getAllPerson',e.data.data.account)
           context.dispatch('getMyActive',e.data.data.account)
         }
       }
     })
   },

  updateAccountState(context, state){
    context.commit('setAccountState', state)
  },
  checkMySort(context, from){
    commonCall(from,'getMySort','', function (resp) {
        console.log('getMySort >>'+JSON.stringify(resp))
    }, function (e) {
        console.error('getMySort e >>'+JSON.stringify(e))
    })
  },

  checkPool(context, from){
    commonCall(from,'checkTotal','', function (resp) {
      console.log('checkTotal >>'+JSON.stringify(resp))
      grobal_state.pool = resp.result
      context.dispatch('updateAccountState', grobal_state)
    }, function (e) {
      console.error('checkTotal e>>'+JSON.stringify(e))
    })
  },

  checkSate(context, from){
    commonCall(from,'checkToday', '', function (resp) {
      console.log('checkSate >>'+JSON.stringify(resp))
      grobal_state.today = resp.result
      context.dispatch('updateAccountState', grobal_state)
      // context.dispatch('updateAccountState', JSON.parse(JSON.stringify(resp.result).remove('\\')));
    }, function (e) {
      console.error('checkSate e >>'+JSON.stringify(e))
    })
  },


  getAllPerson(context, from){
    commonCall(from,'allPerson', '', function (resp) {
      console.log('allPerson >>'+JSON.stringify(resp))
      grobal_state.activePerson = resp.result
      context.dispatch('updateAccountState', grobal_state)
    }, function (e) {
      console.error('allPerson e >>'+JSON.stringify(e))
    })
  },

  getMyActive(context, from){
    commonCall(from,'getActiveDays', '', function (resp) {
      console.log('getMyActive >>'+JSON.stringify(resp))
      grobal_state.activeDays = resp.result
      context.dispatch('updateAccountState', grobal_state)
    }, function (e) {
      console.error('getMyActive e >>'+JSON.stringify(e))
    })
  },

}

export default {
  state,
  getters,
  actions,
  mutations
}



