import NebPay from 'nebpay.js'
var contactAddress = 'n1jFAbPbyU9ZWuwfmEkvdYT6WoYpt3CBnuP'
var nebPay = new NebPay()
var intervalQuery//
const state = {
  serialNumber: '', //
  result: 'waiting',  //
}

var checkOptions = {
  goods: {        //
    name: "example"
  },
  qrcode: {
    showQRCode: false
  },
  extension: {
    openExtension: true //
  },
  //callback ,
  //callback: NebPay.config.mainnetUrl //()
  callback: NebPay.config.testnetUrl //
}

const getters = {
  getResult: (state) => {return state.result},
  getSerialNumber: (state) => {
    return state.serialNumber
  }
}

const mutations = {
  setResult: (state, result) => {state.result = result},
  setSerialNumber: (state, serialNumber) => {
    state.serialNumber = serialNumber
  }
}

const actions = {
  updateResult: (context, result) => { context.commit('setResult'), result},
  updateSerialNumber(context, serialNumber) {
    context.commit("setSerialNumber", serialNumber)
  },
  async startPay(context, count) {
    var to = 'n1EqWJhmtE7agbTDWXkqrbaUmBeS6mptAYH';
    // var value = "0.0001";
    var  serialNumber = await nebPay.pay(to, count, {
      qrcode: {
        showQRCode: false
      },
      goods: {
        name: "test",
        desc: "test goods"
      },
      callback: NebPay.config.testnetUrl,
      listener: (result)=>{ context.commit('setResult', result)},  //set listener for extension transaction result
      debug: true
    });
    context.commit('setSerialNumber', serialNumber)
    intervalQuery = setInterval(function() {
      context.dispatch('funcIntervalQuery', serialNumber)
    }, 10000)
  },

  async startNRC20OPay(context, count) {
    var to = 'n1EqWJhmtE7agbTDWXkqrbaUmBeS6mptAYH';
    var currency ="test" // NRC20, , options.nrc20.symbol
    var serialNumber = await nebPay.nrc20pay(currency, to, count, {
      //nrc20
      nrc20: {
        address: "", //NRC20 
        decimals: 0,    //, 
        name: "",    //,  "Nebulas Token"
        symbol: ""  //,  "NAS", "EOS"
      }
    });
    context.commit('setSerialNumber', serialNumber)
    // funcIntervalQuery()
    intervalQuery = setInterval(function() {
      context.dispatch('funcIntervalQuery', serialNumber)
    }, 10000)
  },

  // async checkMySort(context, count){
  //   var to = contactAddress
  //   var callArgs = ''
  //   var callFunction = "getMySort"
  //   var options =checkOptions
  //   var serialNumber = await nebPay.call(to, count, callFunction, callArgs, options);
  //   if(intervalQuery!==-1){
  //     clearInterval(intervalQuery)
  //   }
  //   intervalQuery = setInterval(function() {
  //     context.dispatch('funcIntervalQuery', serialNumber)
  //   }, 10000)
  // },
  //
  // async checkPoolValue(context, count){
  //   var to = contactAddress
  //   var callArgs = ''
  //   var callFunction = "checkTotal"
  //   var options =checkOptions
  //   var serialNumber = await nebPay.call(to, count, callFunction, callArgs, options);
  //   if(intervalQuery!==-1){
  //     clearInterval(intervalQuery)
  //   }
  //   intervalQuery = setInterval(function() {
  //     context.dispatch('funcIntervalQuery', serialNumber)
  //   }, 10000)
  // },
  //
  // async checkTodaySign(context, count){
  //   var to = contactAddress
  //   var callArgs = ''
  //   var callFunction = "checkToday"
  //   var options =checkOptions
  //   var serialNumber = await nebPay.call(to, count, callFunction, callArgs, options);
  //   if(intervalQuery!==-1){
  //     clearInterval(intervalQuery)
  //   }
  //   intervalQuery = setInterval(function() {
  //     context.dispatch('funcIntervalQuery', serialNumber)
  //   }, 10000)
  // },

  async signToday(context, count){
    var to = contactAddress
    var callFunction = "addDate"
    var callArgs = ''
    var options = {
      goods: {        //
        name: "example"
      },
      qrcode: {
        showQRCode: false
      },
      extension: {
        openExtension: true //
      },
      mobile: {
        showInstallTip: true, //
        installTip: "Please install wallet first" // 
      },
      //callback ,
      //callback: NebPay.config.mainnetUrl //()
      callback: NebPay.config.testnetUrl //
    }
    var serialNumber = await nebPay.call(to, count, callFunction, callArgs, options);
    context.commit('setSerialNumber', serialNumber)
    if(intervalQuery!==-1){
      clearInterval(intervalQuery)
    }
    intervalQuery = setInterval(function() {
      context.dispatch('funcIntervalQuery', serialNumber)
    }, 10000)
  },

  //. queryPayInfoPromise.
  funcIntervalQuery(context, serialNumber) {
  //queryPayInfooptions,(,)
    nebPay.queryPayInfo(serialNumber, { goods: {        //
      name: "example"
    },
      extension: {
        openExtension: false //
      },
      callback: NebPay.config.testnetUrl})   //search transaction result from server (result upload to server by app)
    .then(function (resp) {
      console.log("tx result: " + resp)   //resp is a JSON string
      var respObject = JSON.parse(resp)
      //code==0, status==1
      if (respObject.code === 0 && respObject.data.status === 1) {
        //,....
        context.commit('setResult', respObject)
        clearInterval(intervalQuery)    //
        intervalQuery = -1
      }
    })
    .catch(function (err) {
      console.log(err);
    });
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}




