<template>
  <div class="hello">
    <!--<pan class="lan_selector">-->
    <!--</pan>-->
    <!--<img src="https://nebulas.io/assets/images/nebulasx60.png">-->
    <h1 class="welcome">{{welcome}}</h1>
    <select v-model="selected" @change="onLanguageSelected">
      <option>{{selected}}</option>
      <option v-for="item in languages" v-bind:value="item">{{item}}</option>
    </select>
    <div class="pool">
      <h3>{{pool}}:</h3>
      <h4>{{ accountState.pool }}</h4>
      <label>{{total}}:</label>
      <label>{{ accountState.activeDays }}</label>
      <br/>
      <label>{{sort}}:</label>
      <label>{{ accountState.sort }}</label>
      <h5>{{notice}}</h5>
    </div>
    <p v-on:click="onCourseSelected('Japanese')">{{Japanese_learning}} >></p>
    <p v-on:click="onCourseSelected('Korean')">{{Korean_learning}} >></p>
    <p v-on:click="onCourseSelected('English')">{{English_learning}} >></p>
    <p v-on:click="onCourseSelected('Chinese')">{{Chinese_learning}} >></p>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default {
    name: 'HelloWorld',
    computed: mapGetters({
      languages: 'getSuportLans',
      currentLan: 'getCurLan',
      accountState: 'getAccountState',
      result: 'getResult'
    }),

    created() {
      this.selected = this.currentLan
      this.$store.dispatch('getWallectInfo')
      this.refleshShowLan();
    },

    data() {
      return {
        selected: '',
        welcome: 'Learning is persistence!',
        notice: 'Sign for learning everyday',
        sort: 'Current sort',
        total: 'Active days',
        pool: 'Pool Gas',
        Japanese_learning: 'Start Japanese Learning',
        Korean_learning: 'Start Korean Learning',
        English_learning: 'Start English Learning',
        Chinese_learning: 'Start Chinese Learning',
      }
    },
    methods: {
      onCourseSelected(lang) {new Date().toDateString()
        if(!this.accountState.today){
          this.$store.dispatch("signToday", 0.0001)
          return
        }
        switch (lang){
          case 'Japanese':
//            this.$store.dispatch("signToday", 0.0001)
            this.$router.push(lang)
            break
          case 'Korean':
            this.$store.dispatch("signToday", 0.0001)
            break
          case 'English':
            this.$store.dispatch("checkSate", 0.0001)
            break
          case 'Chinese':
            this.$store.dispatch("checkMySort", 0.0001)
            break
        }
      },
      onLanguageSelected() {
        this.$store.dispatch("changeLan", this.selected)
        this.refleshShowLan()
      },
      refleshShowLan() {
        switch (this.selected) {
          case "English":
            this.welcome = 'Learning is persistence!'
            this.notice =  'Persist in learning everydaythen share NAS in the pool at the next month'
            this.sort = 'Current sort'
            this.total = 'Active days'
            this.pool = 'Pool Gas'
            this.Japanese_learning = 'Start Japanese Learning'
            this.Korean_learning = 'Start Korean Learning'
            this.English_learning = 'Start English Learning'
            this.Chinese_learning = 'Start Chinese Learning'
            break
          case "":
            this.welcome = '!'
            this.notice = '5'
            this.sort = ''
            this.total = ''
            this.pool = 'Gas'
            this.Japanese_learning = ''
            this.Korean_learning = ''
            this.English_learning = ''
            this.Chinese_learning = ''
            break
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  select {
    font-size: 1.2em;
    background: green;
  }

  .welcome{
    font-weight: bold;
    font-size: 2em;
    font-style: italic;
    margin-bottom: 0em;
    color: rgba(6, 39, 43, 0.87);
  }
  .hello {
    text-align: center;
    padding-bottom: 2em;
  }

  .pool{
    margin-top: 2em;
    background: grey;
    padding-top: 0.5em;
  }

  .pool h4{
    margin: 1em;
  }

  .pool label{
    margin-top: 0.5em;
    font-size: medium;
    font-weight: bold;
  }

  .hello p {
    font-style: italic;
    font-size: 1.2em;
    font-family: "Microsoft Sans Serif";
    font-weight: bold;
  }

  p:hover {
    color: #cd7015;;
  }
</style>



